import React from 'react'

const Result = ({ setDisplay, username, score, numberOfQuestions, api }) => {
    return (
        <div>
            <header>
                <a onClick={() => setDisplay('main')} href="/quiz-app">
                    <p className="title-center">Quiz App</p>
                </a>

                <p className="subTitle-center">
                    Cool, {username ? username : 'Player'}!
                </p>
            </header>

            <span id="container">
                <h1 className="final-result">{score}/{numberOfQuestions}</h1>
                <h4 className="final-result-description">
                    {numberOfQuestions - score === 0
                        ? `Amazing Results! You are master!!`
                        : score > 10
                            ? `Excellent Results!`
                            : score > 0
                                ? `Cool Results! Maybe try again?`
                                : score === 0 
                                ? `No Worries, Try Again!` 
                                : null}
                </h4>

                <button className="button" onClick={() => setDisplay('main')}>
                    Try Again!
                </button>


            </span>
        </div>
    )
}

export default Result