import React, { useEffect, useState } from 'react'

const Quiz = (
    { score, setScore,
        api, setApi,
        username,
        numberOfQuestions, // Properties
        category,
        difficulty,
        setDisplay
    }) => {


    // Which Question?...
    const [step, setStep] = useState(1)

    // Handling Loading State Of Page
    const [isLoaded, setIsLoaded] = useState(false)


    // Retrieving Api Data 

    useEffect(() => {
        let triviaApi = `https://opentdb.com/api.php?amount=${numberOfQuestions}${category !== null ? `&category=${category}` : ''}${difficulty !== null ? `&difficulty=${difficulty}` : ''}`
        fetch(triviaApi)
            .then(response => response.json())
            .then(data => {
                setIsLoaded(true)
                setApi(data.results)
            })
            .catch((error) => {
                setIsLoaded(true)
                console.error('Error:', error);
            });

        //eslint-disable-next-line
    }, [])

    // Retrieving Api Data 

    let questions = api


    const answerClick = e => {
        if (step < numberOfQuestions)
            setStep(step + 1)
        else if (step.toString() === numberOfQuestions)
            setDisplay('result')
    }


    return (
        <React.Fragment>
            <div>
                <header>
                    <a onClick={() => setDisplay('main')} href='/quiz-app'><p className="title-center">Quiz App</p></a>
                    <p className="subTitle-center">Test Yourself, {username ? username : 'Player'}!</p>
                </header>

                {/* If loaded, Do This */}
                {isLoaded ?
                    <span id="container" onSubmit={e => e.preventDefault()}>
                        <h2 style={{ textAlign: "center" }}>Question {step}/{`${numberOfQuestions}`}</h2>

                        {questions[step - 1].type === 'multiple' ?
                            // If Multiple Answer Question
                            <>
                                <h4 className="question" dangerouslySetInnerHTML={{ __html: questions[step - 1].question }} />
                                <br />
                                {step % 2 === 0 ?
                                    <>
                                        <h6 id="notCorrect" onClick={answerClick} dangerouslySetInnerHTML={{ __html: questions[step - 1].incorrect_answers[0] }} className="inputs" />
                                        <h6 id="correct" onClick={e => {
                                            answerClick(e)
                                            setScore(score + 1)
                                        }} dangerouslySetInnerHTML={{ __html: questions[step - 1].correct_answer }} className="inputs" />
                                        <h6 id="notCorrect" onClick={answerClick} dangerouslySetInnerHTML={{ __html: questions[step - 1].incorrect_answers[1] }} className="inputs" />
                                        <h6 id="notCorrect" onClick={answerClick} dangerouslySetInnerHTML={{ __html: questions[step - 1].incorrect_answers[2] }} className="inputs" />
                                    </>
                                    : <>
                                        <h6 id="notCorrect" onClick={answerClick} dangerouslySetInnerHTML={{ __html: questions[step - 1].incorrect_answers[2] }} className="inputs" />
                                        <h6 id="notCorrect" onClick={answerClick} dangerouslySetInnerHTML={{ __html: questions[step - 1].incorrect_answers[1] }} className="inputs" />
                                        <h6 id="notCorrect" onClick={answerClick} dangerouslySetInnerHTML={{ __html: questions[step - 1].incorrect_answers[0] }} className="inputs" />
                                        <h6 id="correct" onClick={e => {
                                            answerClick(e)
                                            setScore(score + 1)
                                        }} dangerouslySetInnerHTML={{ __html: questions[step - 1].correct_answer }} className="inputs" />
                                    </>
                                }
                            </> :
                            // If Boolean Type Question
                            <>
                                <h4 style={{ textAlign: 'center' }} dangerouslySetInnerHTML={{ __html: questions[step - 1].question }} />
                                <br />
                                {step % 2 === 0 ?
                                    <>
                                        <h6 id="correct" onClick={e => {
                                            answerClick(e)
                                            setScore(score + 1)
                                        }} className="inputs">{questions[step - 1].correct_answer}</h6>
                                        <h6 id="notCorrect" onClick={answerClick} className="inputs">{questions[step - 1].incorrect_answers}</h6>
                                    </>
                                    : <>
                                        <h6 id="notCorrect" onClick={answerClick} className="inputs">{questions[step - 1].incorrect_answers}</h6>
                                        <h6 id="correct" onClick={e => {
                                            answerClick(e)
                                            setScore(score + 1)
                                        }} className="inputs">{questions[step - 1].correct_answer}</h6>
                                    </>
                                }
                            </>
                        }
                    </span>

                    // If loading, do below...
                    : <span id="container">
                        <h1 style={{
                            display: 'flex',
                            justifyContent: 'center',
                            alignItems: 'center',
                            height: '25vh'
                        }}>Loading...</h1>
                    </span>}
            </div>

        </React.Fragment>
    )
}

export default Quiz